import React from 'react';
// eslint-disable-next-line
import ReactDOM from 'react-dom';
import './App.css';

function App() {
  function getRandomInt(min, max) {
    min = Math.ceil(min);
    max = Math.floor(max);
    return Math.floor(Math.random() * (max - min)) + min;
  }

  //Variável de pontuação
  const [pontos, setPontos] = React.useState(
    localStorage.getItem('pontos') == true
      ? localStorage.setItem('pontos', 0)
      : parseInt(localStorage.getItem('pontos')),
  );

  //Variável de Dificuldade
  const [difficult, setDificult] = React.useState(
    localStorage.getItem('difficult')
      ? parseInt(localStorage.getItem('difficult'))
      : localStorage.setItem('difficult', 0),
  );

  let n1;
  let n2;
  // eslint-disable-next-line
  if (difficult == 1) {
    n1 = getRandomInt(2, 10);
    n2 = getRandomInt(2, 10);
  } else {
    n1 = getRandomInt(3, 20);
    n2 = getRandomInt(3, 20);
  }

  //Variáveis das contas
  // eslint-disable-next-line
  const [num1, setNum1] = React.useState(n1);
  // eslint-disable-next-line
  const [num2, setNum2] = React.useState(n2);

  //Limpador de input
  //var ivalue = '';

  // Função que troca os números
  // function trocaNum() {
  //   eslint-disable-next-line
  //   if (difficult == 1) {
  //     setNum1(getRandomInt(2, 10));
  //     setNum2(getRandomInt(2, 10));
  //   } else {
  //     setNum1(getRandomInt(3, 20));
  //     setNum2(getRandomInt(3, 20));
  //   }
  // }

  function trocaDificuldade() {
    // eslint-disable-next-line
    if (difficult == 0) {
      setDificult(1);
      document.location.reload(true);
      // eslint-disable-next-line
    } else if (difficult == 1) {
      setDificult(0);
      document.location.reload(true);
    }
  }
  localStorage.setItem('difficult', difficult);

  function verify() {
    //Variável de Resultado
    var result = document.getElementById('result').value;
    result = parseInt(result);
    setPontos(parseInt(pontos));
    setPontos(pontos == NaN ? 0 : pontos);

    // eslint-disable-next-line
    if (result == num1 * num2) {
      setPontos(pontos + 1);
    } else {
      setPontos(pontos - 1);
    }
  }

  localStorage.setItem('pontos', pontos);

  return (
    <div id="main">
      <button className="difficult" onClick={trocaDificuldade}>
        {
          // eslint-disable-next-line
          difficult == 1 ? 'Fácil' : 'Médio'
        }
      </button>

      <h1 className="num num1">{num1}</h1>
      <h2>x</h2>
      <h1 className="num num2">{num2}</h1>
      <hr id="divisor" />
      <form>
        <input
          type="number"
          name="result"
          id="result"
          placeholder="resultado"
        />{' '}
        <br />
        <input type="submit" onClick={verify} className="enviar" />
      </form>

      <h2>Pontos:</h2>
      <h1>{pontos}</h1>

      <button
        onClick={() => {
          localStorage.setItem('pontos', 0);
          localStorage.setItem('difficult', 1);
        }}
        className="zerar"
      >
        Zerar
      </button>
    </div>
  );
}

export default App;
